import React from "react"
import Grid from "@mui/material/Grid"
import "./Card.scss"
function Card({ cardData, col, gap }) {
  return (
    <div className="blog-card">
      <Grid container spacing={gap} justifyContent="center">
        {cardData.map((card, i) => {
          return (
            <Grid item md={col} key={i}>
              <div className="img-wrapper text-center">
                <img src={card.img} alt="img" className="img-fluid" />
              </div>
              <p className="date-card">{card.date}</p>
              <p className="header-card">{card.header}</p>
              <p className="text-card">{card.text.substring(0, 400)}</p>
            </Grid>
          )
        })}
      </Grid>
    </div>
  )
}

export default Card
